import {KsCourseList} from 'components/ks-components/course-list/course-list'

import {GetHome} from 'api/index'
export default {
	components:{KsCourseList},
	data() {
		return {
			courseList:[],
			bannerList:[],
			moduleList:[],
			key:'',
			state:{},
			isApp:false,
			appInitPic:false,
			isshowprice:false,
			baseUrl:this.$store.state.SRC+"/config/app"
		}
	},
	onShow() {
		
	},
	onLoad() {
		if(!uni.getStorageSync('appInitPic')){
			this.appInitPic = true
		}
		this.$store.ready(state=>{
			this.isshowprice = state.isshowprice
			this.initData()	
		})
		
	},
	onNavigationBarSearchInputConfirmed(query){
		uni.navigateTo({
			url:'/pages/course/search?key='+query.text
		})
		this.key = ''
	},
	onPullDownRefresh() {
		this.initData() 
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　 　title: '科汛网校v7.0',
			path: '/pages/index/index',
	　　　　imageUrl: '', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		openIndex(index){
			// console.log(index)
		},
		initData(){
			this.$store.ready(state=>{
				uni.showLoading({
					title:'加载中'
				});
				this.state = state
				GetHome().then(res=>{
					this.bannerList = res.chart
					this.moduleList = res.modulelist
					this.courseList = res.showinfo
					this.$nextTick(()=>{
						uni.hideLoading()
						uni.stopPullDownRefresh();
					})
				})
			})
			
		},
		openTestPage(){
			uni.navigateTo({
				url:'/pages/order-pay/order-pay'
			})
		},
		scroll: function(e) {
			// console.log(e)
		},
		search(){
			if(this.key){
				var title = this.key
				this.key = ''
				uni.navigateTo({
					url:'/pages/course/search?key='+title
				})
			}
		},
		open(item){
			let url
			if(item.type=='teacher'){
				url = '/pages/teacher/index'
			}else if(item.type=='class'){
				url = '/pages/class/index'
			}else if(item.type=='sj'){
				url = '/pages/exam/index'
			}else if(item.type=='school'){
				url = '/pages/school/index'
			}
		
			uni.navigateTo({
			    url: url
			});
		},
		openView(url){
			uni.navigateTo({
			    url: url
			});
		}
	}
}
